body {
    background-image: url(images/eva12.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: goldenrod;
    font-family: 'Amatic SC', cursive ;
    /* background-color: rgb(95, 207, 128); */
}

p {
    font-size: 4rem;
    font-weight: 400;
    line-height: 1.1;
    text-shadow: 0 1px 1px rgba(0,0,0,.8);
    position: relative;
    margin: 0;
}



#quote-box {
    position: absolute;
    top: 14%;
    left: 10%;
    right: 10%;
    width: 80%;
    line-height: .5;
}


 .quote {
    font-size: 4rem;
    font-weight: 400;
    line-height: 1.1;
    text-shadow: 0 1px 1px rgba(0,0,0,.8);
    position: relative;
    margin: 0;
}

.quote::before, .quote::after {
    font-size: 6rem;
    line-height: 2.5rem;
    position: absolute;
}

.quote::before {
    content: '"';
    top: .25em;
    left: -.5em;
}
 .quote::after {
    content: '"';
    bottom: -.01em;
    margin-left: .1em;
    position: absolute;
 }

 

 
 #loadQuote {
    font-size: 2.5rem;
    position: fixed;
    width: 7em;
    display: inline-block;
    left: 50%;
    margin-left: -6em;
    bottom: 30px;
    border-radius: 4px;
    border: 2px solid #fff;
    color: #fff;
    background-color: rgba(255, 255, 255, 0);
    padding: 15px 0;
    transition: .5s;
}

#loadQuote:hover {
    background-color: rgba(255, 255, 255, .25);
}

#loadQuote:focus {
    outline: none;
}

@media (max-width: 420px) {
    .quote {
        font-size: 2.5rem;
    }

    .quote::before, .quote::after {
        font-size: 3rem;
    }

    .source {
        font-size: 1rem;
    }
}

